import { createStore, applyMiddleware, compose } from 'redux'
import thunk from 'redux-thunk'

import rootReducer from './index'
import config from '../../../config'

const IS_CLIENT_ENV = typeof window !== 'undefined'

const middlewares = [thunk]

// === redux(): redux === //
// === 1  chrome : redux Devtools === //
// === 2  compose : window.devToolsExtension() === //
// === 3 chromeredux,  === //

let storeEnhancers =
  config.ISDEV && IS_CLIENT_ENV
  ? compose(  //  + 
    applyMiddleware(...middlewares),
    window.devToolsExtension ? window.devToolsExtension() : f => f
    )
  : compose(  //  +  || 
    applyMiddleware(...middlewares)
  )

export function configureStore (initialStore = {}) {
  return createStore(
    rootReducer,
    initialStore,
    storeEnhancers
  )
}

// let store = IS_CLIENT_ENV ? configureStore({}) : {}
let store = configureStore({})

/**
 * store
 * @param {Object = {}} initialStore store
 * @return {Object}
 */
export default store
